<template>
  <div>
    <audio src="" ref="audio"></audio>
    <div>
      <div style="margin-bottom: 20px">
        {{ currentAudio.name }}
      </div>
      <div style="margin-bottom: 20px">
        播放速度：<input type="mobile" v-model="speed" />
      </div>
      <div>
        <button @click="roundPlay" v-if="!playing">开始循环</button>
        <button @click="stopPlay" v-else>停止播放</button>
      </div>
    </div>
  </div>
</template>

<script>
function getCurrentAudio() {
  return {
    name: `当前暂无播放`,
  };
}

export default {
  data() {
    return {
      speed: 3000,
      playing: false,
      roundTimer: null,
      //当前播放
      currentAudio: getCurrentAudio(),
      //常规列表
      audioRandomList: [
        {
          name: `前方通过路口`,
          src: `/audio/dipped-headlights-0.mp3`,
        },
        {
          name: `夜间同方向近距离跟车行驶`,
          src: `/audio/dipped-headlights-1.mp3`,
        },
        {
          name: `夜间与机动车会车`,
          src: `/audio/dipped-headlights-2.mp3`,
        },
        {
          name: `夜间在良好得道路上行驶`,
          src: `/audio/dipped-headlights-3.mp3`,
        },
        {
          name: `夜间在无照明或照明不良的道路上行驶`,
          src: `/audio/distance-light.mp3`,
        },
        {
          name: `夜间超越前方车辆`,
          src: `/audio/flashing-light-0.mp3`,
        },
        {
          name: `夜间通过急弯坡路拱桥人行横道`,
          src: `/audio/flashing-light-1.mp3`,
        },
        {
          name: `夜间通过无信号灯路口`,
          src: `/audio/flashing-light-2.mp3`,
        },
        {
          name: `夜间在路边临时停车`,
          src: `/audio/stop-light.mp3`,
        },
      ],
      //循环排序列表
      randomPlaySort: [],
    };
  },
  methods: {
    getAudioRef() {
      return this.$refs[`audio`];
    },
    getRandomAudio() {
      const getIndex = this.randomPlaySort.shift();
      return this.audioRandomList[getIndex];
    },
    randomSortIndex() {
      const len = this.audioRandomList.length;
      const arr = [];
      for (let i = 0; i < len; i++) {
        arr.push(i);
      }
      this.randomPlaySort = arr.sort(() => {
        return Math.random() - 0.5;
      });
    },
    randomPlay() {
      const audioRef = this.getAudioRef();
      if (this.randomPlaySort.length === 0) {
        this.randomSortIndex();
      }
      const currentAudio = this.getRandomAudio();
      audioRef.src = currentAudio.src;
      this.currentAudio.name = currentAudio.name;
    },
    initEvent() {
      const audioRef = this.getAudioRef();
      audioRef.addEventListener(`ended`, (event) => {
        console.log(event);
        this.roundTimer = setTimeout(() => {
          this.randomPlay();
        }, this.speed);
      });
      audioRef.addEventListener(`error`, (error) => {
        console.log(error);
      });
      audioRef.addEventListener(`loadedmetadata`, () => {
        audioRef.play();
      });
    },
    roundPlay() {
      //设置排序
      this.randomSortIndex();
      this.randomPlay();
      this.playing = true;
    },
    //暂停播放
    stopPlay() {
      const audioRef = this.getAudioRef();
      audioRef.pause();
      this.playing = false;
      clearTimeout(this.roundTimer);
      this.currentAudio = getCurrentAudio();
    },
  },
  mounted() {
    this.initEvent();
  },
};
</script>

<style scoped>
button,
input {
  height: 30px;
  line-height: 30px;
  box-sizing: border-box;
}
</style>
